<template>
    <div>
        <v-container fluid class="pa-0">
            <v-row>
                <v-col cols="12" md="8">
                    <v-btn
                        large
                        @click="appTypeMenu = !appTypeMenu"
                        :outlined="appTypeMenu ? true : false"
                        color="white"
                    >
                        <v-icon
                            class="mr-2"
                            :color="appTypeMenu ? 'white' : '#4252af'"
                            :class="appTypeMenu ? 'mdi-spin' : ''"
                        >
                            mdi-cog
                        </v-icon>
                        <span
                            :style="{
                                color: appTypeMenu ? 'white' : '#4252af',
                            }"
                        >
                            app type
                        </span>
                    </v-btn>
                </v-col>
                <v-col cols="12" md="4">
                    <v-text-field
                        clearable
                        label="Name Application . . ."
                        solo
                        hide-details="auto"
                        v-model="search"
                        append-icon="mdi-magnify"
                        @click:append="readMail"
                    ></v-text-field>
                </v-col>
                <transition name="fade" mode="out-in">
                    <v-col cols="12" v-show="appTypeMenu">
                        <v-sheet class="pa-4" rounded>
                            <v-chip-group
                                active-class="primary--text"
                                column
                                v-model="projectTypeSelected"
                                multiple
                            >
                                <v-chip
                                    v-for="(n, i) in projectType"
                                    :key="i"
                                    label
                                    filter
                                >
                                    {{ n.name }}
                                </v-chip>
                            </v-chip-group>
                        </v-sheet>
                    </v-col>
                </transition>
                <v-col cols="12">
                    <ListProject />
                </v-col>
            </v-row>
        </v-container>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
    name: "Project",

    components: {
        ListProject: () =>
            import(/* webpackChunkName: "navbar" */ "./ListProject.vue"),
    },

    data() {
        return {
            appTypeMenu: false,
            projectTypeSelected: [],
            app_type: "",
            search: "",
        };
    },

    computed: {
        ...mapGetters({
            projectType: "getProjectType",
        }),
        currentRouteName() {
            return this.$route.meta.title;
        },
    },

    methods: {
        readMail() {
            alert("tes");
        },
    },

    metaInfo() {
        return {
            // title: 'tes',
            // override the parent template and just use the above title only
            titleTemplate: "%s | " + this.currentRouteName,
            meta: [
                {
                    vmid: "title",
                    name: "title",
                    content: this.currentRouteName,
                },
                {
                    vmid: "description",
                    name: "description",
                    content: "bar",
                },
            ],
        };
    },
};
</script>

<style scoped>
</style>